<template>
  <div>
    <div class="new_employee_header">
      <span class="new_employee_title">New employee</span>
      <PersonPannel></PersonPannel>
    </div>
    <form action="" class="new_employee_settings">
      <TextInput @emitTextInput="newEmployeeUsernameChange" label="Username" placeholder="Type name here..."></TextInput>
      <TextInput @emitTextInput="newEmployeeEmailChange" label="Email" placeholder="Type email here..."></TextInput>
      <TextInput @emitTextInput="newEmployeePhoneChange" label="Contact phone" placeholder="Type phone here..."></TextInput>
      <TextInput @emitTextInput="newEmployeeCreditChange" label="Credit" placeholder="..."></TextInput>
      <AppButton text="add employee" :type="'danger'" @action="createNewEmployee"></AppButton>
    </form>
  </div>
</template>

<script>
import { mapState } from 'vuex'

import AppButton from '@/components/AppButton.vue'
import TextInput from '@/components/TextInput.vue'
import PersonPannel from '@/components/PersonPannel.vue'
export default {
  data () {
    return {
      newEmployeeUsernameModel: '',
      newEmployeeEmailModel: '',
      newEmployeePhoneModel: '',
      newEmployeeCreditModel: ''
    }
  },
  computed: {
    ...mapState(['employeesData'])
  },
  methods: {
    newEmployeeUsernameChange (data) {
      this.newEmployeeUsernameModel = data
    },
    newEmployeeEmailChange (data) {
      this.newEmployeeEmailModel = data
    },
    newEmployeePhoneChange (data) {
      this.newEmployeePhoneModel = data
    },
    newEmployeeCreditChange (data) {
      this.newEmployeeCreditModel = data
    },
    defaultModel (model, value) {
      if (model === '') {
        return value
      } else {
        return model
      }
    },
    createNewEmployee () {
      this.employeesData.push({
        id: this.employeesData.length + 1,
        username: this.defaultModel(this.newEmployeeUsernameModel, 'No Name'),
        email: this.defaultModel(this.newEmployeeEmailModel, 'No Email'),
        phone: this.defaultModel(this.newEmployeePhoneModel, 'No Phone'),
        credits: this.defaultModel(this.newEmployeeCreditModel, 0),
        tokens: 0,
        status: 'active'
      })
      this.$router.push('/dashboard/employees')
    }
  },
  components: {
    AppButton,
    TextInput,
    PersonPannel
  }
}
</script>
